@import "normalize";

$black: #212121;
$green:#00897b;
$roboto: 'Roboto', sans-serif;
$white-green:#e4fdf9;
.container{
    margin: 0 auto;
    max-width: 1170px;
 
}


body{
    font-family: 'Open Sans', sans-serif;
    color: $black;
}

p{
    font-size: 15px;
    font-weight: 400;
}

h3{
    font-weight: 600;
    font-size: 20px;
}
h2{
    font-weight: bold;
}

h1,h2,h3,h4,h5{
    padding: 0;
    margin: 0;
}

header{ 
    padding-top: 27px;
    padding-bottom: 36px;
    .header{
        display: flex;
        justify-content: space-between;
        width:100%;
        max-width: 1170px;
        align-items: center;
@media (max-width:1208px) {
    flex-direction: column;
 
}

        .logo{
            text-decoration: none;
            font-size: 25px;
            font-weight: bold;
           color: $black;
        }
        &__item{
            display: flex;
            justify-content: flex-end;
            list-style-type: none;
            @media (max-width:858px) {
               flex-direction: column; 
               
            }
            li{
                margin-left: 45px;
                @media (max-width:858px) {
                   margin-top: 20px;
                   margin-left: 0;
                }
                a{
                    text-decoration: none;
                    font-size: 20px;
                    color: $black;
                }
            }
        }
    }
}

.page-header{
    background: url("../img/headbg.jpg") no-repeat center;
    background-size: cover;
    color:#fff;
    padding-top: 172px;
    padding-bottom: 67px;
    &__content{
           margin: 0 auto;
           text-align: center;
          
            &__title{
              
                font-size: 60px;
                font-weight: 600;
                letter-spacing: 3px;
                @media(max-width:850px){
                    font-size: 40px;
                }
            }
            &__subtitle{
                font-size: 22px;
                letter-spacing: 1.1px;
                @media(max-width:850px){
                    font-size: 15px;
                }
            }

           
          
            &__buttons{
            display: flex;
        justify-content: center;
        padding-top: 90px;
        padding-bottom: 96px;
        @media (max-width:850px) {
            flex-direction: column;
          
        }
       
              a{
            
                color: #fff;
                text-decoration: none;
                font-family: $roboto;
                background: transparent;
                padding: 21px 45px;
                border: 3px solid #f8f8f8;
                font-weight: bold;
                font-size: 20px;
                display: inline-block;
                max-width: 215px;
                margin-right: 11px;
                @media (max-width:850px) {
                    max-width:  initial;
                  margin-top: 10px;
                }
                }
               
                    .active_button{
                        background: $green;
                        border: none;
                    }
                 
            }

            i{
                color: #f8f8f8;
                font-size: 25px;
            }
    }
}

.page-about{
    padding-top: 121px;
    padding-bottom: 109px;

    &__content{
        margin: 0 auto;
        text-align: center;
        &__title{
            
            font-size: 30px;
            letter-spacing: 2.2px;
            padding-bottom: 28px;
        }
        &__subtitle{
            max-width: 633px;
            margin: 0 auto;
            letter-spacing: 0.7px;
            line-height: 1.3;
        }
    
    &__plus{
        padding-top: 125px;
        display: flex;
        justify-content: space-around;
        align-items: center;
        @media (max-width:858px){
            flex-direction: column;
            padding-top:19px;
            
        }
        &__item{
            flex-basis:23%;
            img{
                padding-bottom: 35px;
                @media (max-width:858px){
                    padding-bottom: 24px;
                    padding-top: 40px;
                    
                }
            }
           
            p{
                letter-spacing: 2px;
                line-height: 1.5;
                @media (max-width:858px){
                    max-width: 332px;
                    margin: 0 auto;
                    margin-top: 18px;
                }
            }
        }
    }
}
}
.white-green{
    background: $white-green;
}
.page-services{
    
    padding-top: 167px;
    padding-bottom: 118px;
    &__content{
        text-align: center;
        &__title{
          
            font-size: 30px;
            letter-spacing: 2.2px;
            padding-bottom: 28px;
        }
        &__subtitle{
            max-width: 633px;
            margin: 0 auto;
            letter-spacing: 0.7px;
            line-height: 1.3;
        }

        &__block{
            display: grid;
            grid-template-columns: 370px 370px 370px;
            grid-template-rows: 438px 438px;
            grid-column-gap: 30px;
            grid-row-gap: 33px;

            padding-top: 114px;
            @media (max-width:1215px) {
         justify-content: center;
            grid-template-columns: 370px 370px;
            grid-template-rows: 438px 438px 438px;
            }
            @media (max-width:820px) {
                justify-content: center;
                   grid-template-columns: 286px;
                   grid-template-rows: 438px 438px 438px 438px 438px 438px;
                   }
            &__item{
            background: #fff;
            text-align: center;
            transition: 0.2s ease-in-out;
            position: relative;
               img{
                    padding-top: 58px;
                    padding-bottom: 34px;
                  
                }
              
                
                h3{
                    padding-bottom: 18px;
                }
               &__text{
                margin: 0 auto;
    max-width: 303px;
    letter-spacing: 1px;
    padding-bottom: 51px;
    line-height: 1.5;
                }
                a{
                    display: block;
                    text-decoration: none;
                    text-transform: uppercase;
                    font-weight: 600;
                    font-size: 20px;
                    color: $black;
                    max-width: 152px;
                    margin: 0 auto;
                    letter-spacing: 1px;
                    border: 2px solid $black;
                    padding: 14px 0;
                }
                i{
                    position: absolute;
                    font-size: 30;
                    right: 173px;
                    bottom: 39px;
                    display: none;
                    @media (max-width:820px) {
                        right: 138px;
                        bottom: 40px;
                           }
                }
            }
            &__item:hover{
                background:#00897b;
                color: #fff;
                transition: 0.2s ease-in-out;
                
                a{
                    color: #fff;
                    border:2px solid #fff;
                }
                i{
                    display: block;
                }
            }

        }
    }
}
.smartphone-white{
    display: none;
}

.show{
    display: initial;
}
.hide{
    display: none;
}


.page-works{
    padding-top: 123px;
    padding-bottom: 140px;
    &__content{
        text-align: center;
        &__title{
          
            font-size: 30px;
            letter-spacing: 2.2px;
            padding-bottom: 28px;
        }
        &__subtitle{
            max-width: 633px;
            margin: 0 auto;
            letter-spacing: 0.7px;
            line-height: 1.3;
        }

        &__buttons{
            padding-top: 116px;
            padding-bottom: 109px;
            display: flex;
            justify-content: center;
            @media (max-width:720px) {
                flex-direction: column;
                
            }
            a{
                display: inline-block;
                text-decoration: none;
                text-transform: uppercase;
                font-weight: 600;
                font-size: 20px;
                color: #212121;
                max-width: 152px;
               
                letter-spacing: 1px;
                border: 2px solid #212121;
                padding: 14px 25px;
                margin-right: 8px;
                transition: 0.3s ease-in-out;
                @media (max-width:720px) {
                 max-width: initial;
                    margin-top: 10px;
                    
                }
            }
            a:hover{
                color: #fff;
                background: $green;
                border:2px solid $green;
                transition: 0.3s ease-in-out;
                cursor: pointer;
            }
        }
    }
        &__block{
            display: grid;
            grid-template-columns: 370px 370px 370px;
            grid-template-rows: 373px 373px;
            grid-column-gap: 30px;
            grid-row-gap: 30px;
            padding-bottom: 124px;
            @media (max-width:1215px) {
                grid-template-columns: 370px 370px ;
                grid-template-rows: 373px 373px 373px;
                justify-content: center;
            }
            @media (max-width:820px) {
                grid-template-columns: 291px;
                grid-template-rows: 331px 331px 331px 331px;
                justify-content: center;
            }
            &__item{
                    overflow: hidden;
                 position: relative;
                 @media (max-width:820px) {
                   overflow: initial;
                }
                    img{
                        transition: 1s ease-in-out;
                        @media (max-width:820px) {
                            width: 100%;
                         }
                    }
                   
                
            img:hover{
            
                @media (max-width:820px) {
                   transition: none;
                   transform: none;
                 }
                transform: scale(1.1);
                transition: 1s ease-in-out;

                }
              

                
            }
    }
    &__btnAll{
        display: block;
        text-decoration: none;
        text-transform: uppercase;
        font-weight: 600;
        font-size: 20px;
        color: $green;
        max-width: 215px;
        margin: 0 auto;
        letter-spacing: 1px;
       
        padding: 22px 0;
       
        text-align: center;
        color: #fff;
        background: #00897b;
        font-family: $roboto;

        transition: 0.3s ease-in-out;
    }
    &__btnAll:hover{
        border: 2px solid $black;
        color:$black;
        background: transparent;
        transition: 0.3s ease-in-out;
        padding: 20px 0;
    }
}

.page-gray{
    background: #f0f1f1;
}

.page-plan{
    padding-top: 122px;
