                grid-column-gap: 30px;
                grid-row-gap: 30px;
                justify-content: center;
            }
            @media (max-width:425px) {
                grid-template-columns: 280px ;
            
            }
           
            &__item{
                background: #fff;
                transition: 1s ease-in-out;
                &__prices{    
                    padding-top: 17px;
                    padding-bottom: 56px;
                    background: #3949ab;
                    p{
                        color:#fff;
                        text-transform: capitalize;
                        font-size: 25px;
                        letter-spacing: 1.2px;
                    }
                    h4{
                        color:#fff;
                      align-items: center;
                        font-size: 50px; 
                        span{
                            font-size: 17px;
                        }
                    }
                }
            /*    &__prices::after{
                   
                }*/
                &__features{
                    max-width: 170px;
                    margin: 0 auto;
                    padding-top: 18px;
                    padding-bottom: 54px;
                    p{
                        font-size: 17px;
                        font-weight: 300;
                        padding-top: 14px;
                        letter-spacing: 0.8px;

                    }
                    p::after{
                        padding-top: 11px;
                        display: block;
                        width: 100%;
                        content: "";
                        border-bottom: 1px solid #bfbfbf;
                    }
                  
                }
                &__button{
                    display: block;
                    text-decoration: none;
                    text-transform: uppercase;
                    font-weight: 600;
                    font-size: 20px;
