                    max-width: 175px;
                    margin: 0 auto;
                    letter-spacing: 1px;
                    border: 2px solid #212121;
                    padding: 14px 0;
                    transition: 0.3s ease-in-out;
                    cursor: pointer;
                }
                &__button:hover{
                    color: #fff;
                    background: $green;
                    border:2px solid $green;
                    transition: 0.3s ease-in-out;
                    cursor: pointer;
                }
            }
            &__item:hover{
                box-shadow: 1px 2px 9px 8px #646463cc;
                transition: 0.4s ease-in-out;
            }
        }
    }
}

