.page-dark{
    background: url("../img/contactbg.jpg") no-repeat center;
    background-size: cover;
}

.page-contact{
    color: #fff;
    padding-top: 120px;
    padding-bottom: 109px;
    &__content{
        text-align: center;
        padding-bottom: 102px;
        @media (max-width:750px) {
            padding-bottom: 20px;
        }
        &__title{
          font-weight: bold;
            font-size: 30px;
            letter-spacing: 2.2px;
            padding-bottom: 28px;
        }
        &__subtitle{
            max-width: 633px;
            margin: 0 auto;
            letter-spacing: 0.7px;
            line-height: 1.3;
            @media (max-width:686px) {
                font-size: 13px;
                max-width: 533px;
            }
        }

    }

    &__address{
        display: flex;
        justify-content: space-around;
        text-align: center;
        padding-bottom: 61px;
        @media (max-width:760px) {
            flex-direction: column;
            margin-top: 20px;
        }
        &__item{
            flex-basis: 20%;
            img{
                padding-bottom: 17px;
            }
            p{
                font-size: 20px;
                font-weight: 600;  
                @media (max-width:760px) {
                   font-size: 15px;
                }
            }
        }
    }

    &__form{
        text-align: center;
        font-weight: 600;
        font-size: 20px;   

        &__name,&__email{
            box-sizing: border-box;
            width: 100%;
            max-width: 498px;
            margin-right: 9px;
            margin-left: 16px;
            border-radius: 10px;
            padding: 31px 20px;
            display: inline-block;
            outline: none;
            @media (max-width:1110px) {
                margin-right: 0;
            margin-left: 0;
                margin-top: 20px;
            }
            @media (max-width:800px) {
                
                max-width: 284px;
    
            }
        }
        &__text{
        box-sizing: border-box;
        width: 100%;
        border-radius: 10px;
        display: block;
        max-width: 1030px;
        padding: 20px 22px;
        margin: 0 auto;
        margin-top: 33px;
        resize: none;
        outline: none;
        letter-spacing: 1.2px;
        @media (max-width:800px) {
                
            max-width: 284px;

        }
        }
        &__button{
            width: 223px;
            max-width: 223px;
        
            letter-spacing: 1px;
            padding: 25px 0;
            text-align: center;
            margin-top: 36px;
          
            color: #000;
            border-radius: 10px;
            background: #fff;
            border: 1px solid #00897b;
            outline: none;
            font-family: "Roboto",sans-serif;
            cursor: pointer;
        }
    }
    
}

